<template>
  <div>
    <div class="context" v-html="content">

    </div>
    <div class="mycanvas">
      <canvas class="canvas">
        <div class="img-box">
          <img width="100%" src="../assets/coder.png" alt="">
        </div>
      </canvas>
    </div>
  </div>
</template>
<style scoped>
.mycanvas {
  min-width: 280px;
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
}

.canvas {
  width: 100%;
  height: calc(100%/3*2)
}

.img-box {
  max-width: 200px;
  margin: 20px auto;
}

p {
  margin-top: 10px;
}
</style>
<script>
export default {
  data () {
    return {
      content: '<h1 class="text-center">Welcome</h1><h3 class="text-center">这里是我的个人主页</h3><p class="text-center margin-top">用作记录工作中、和学习中的一些技术相关问题</p><p class="text-center margin-top">别害羞、来看看我踩过的坑吧</p>'
    }
  },
  mounted () {
    var [context, progress, _self] = [this.content, 0, this]
    this.content = ''
    var timer = setInterval(function () {
      var current = context.substr(progress, 1)
      if (current === '<') {
        progress = context.indexOf('>', progress) + 1
      } else {
        progress++
      }
      _self.content = context.substring(0, progress) + (progress & 1 ? '_' : '')
      if (progress >= context.length) {
        clearInterval(timer)
      }
    }, 100)
    window.S.init()
  }
}
</script>



